
export default {
  props: {
    text: String,
    primaryAction: String,
    secondaryAction: String,
    icon: String,
  },
  setup(props, { emit }) {
    const onPrimary = () => emit('click', { target: 0 });
    const onSecondary = () => emit('click', { target: 1 });
    return { onPrimary, onSecondary };
  },
};
