
import githubLogo from './assets/github-logo.svg';

export default {
  props: { scrollTarget: HTMLElement },
  emits: ['toggle'],
  data() {
    return {
      githubLogo,
    };
  },
  methods: {
    goto(href) {
      window.open(href, '_blank');
    },

    onNav() {
      this.$emit('toggle');
    },
  },
};
